window.addEventListener('load', function(e) {

	// target the right page
	var faqs = document.querySelectorAll('ul.faq');
	if (!faqs.length) {
		return;
	}

	init();

	/**
	 * Initializes the page
	 */
	function init() {
		getELements();
		addHandlers();
	}

	/**
	 * Gets references to the elements on the page
	 */
	function getELements() {
		//btnPasnummer = document.getElementById('btn-pasnummer');
	}

	/**
	 * Sets event listeners for the page
	 */
	function addHandlers() {
		//btnPasnummer.addEventListener('click', onClickBtnPasnummer);
		var buttons = [];
		Array.prototype.forEach.call(faqs, function(faq) {
			var btns = faq.querySelectorAll('.faq-button');
			Array.prototype.forEach.call(btns, function(btn) {
				buttons.push(btn);
			});
		});

		buttons.forEach(function(button) {
			console.log(button.innerText);
			button.addEventListener('click', onClickFaqButton);
		})
	}

	function onClickFaqButton(e) {
		var button = e.target;
		var answer = button.parentNode.parentNode.querySelector('.faq-answer');
		console.log(answer.innerText);
		var isPressed = button.getAttribute('aria-pressed') !== 'true';
		button.setAttribute('aria-pressed', isPressed ? 'true' : 'false');
		answer.style.display = isPressed ? 'block' : 'none';
	}
});
